.select2-selection--single {
  box-sizing: border-box;

  cursor: pointer;
  display: block;

  //height: 28px;
  // height: $normalHeight;
  .m-info & {
    // height: $bigHeight;
  }
  user-select: none;
  -webkit-user-select: none;

  .select2-selection__rendered {
    display: block;
    padding-left: 8px;
    padding-right: 20px;

    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .select2-selection__clear {
    position: relative;
  }

  .staff-head{
    display: inline-block;
  }
  .staff-name{
    display: inline-block;
    line-height: 40px;
    vertical-align: top;
  }
}

&[dir="rtl"] {
  .select2-selection--single {
    .select2-selection__rendered {
      padding-right: 8px;
      padding-left: 20px;
    }
  }
}
